.AboutMe {
  padding: 3rem 2rem;
  display: flex;
  justify-content: center;
  background-color: #323031;
  color: #fff;
  font-family: "IBM Plex Sans KR", sans-serif;
}

.AboutMe_Title {
  display: flex;
  justify-content: center;
}

.AboutMe_Text {
  font-size: 3rem;
  color: #fff;
  border-bottom: 1px solid;
}

.AboutMe_Content {
  display: flex;
  text-align: center;
  padding: 1rem 1rem;
}

.AboutMe_Content_Text {
  font-size: clamp(1rem, 5vw, 2.5rem);
  margin-top: 30px;
  letter-spacing: 2px;
}

.AboutMe_MoreBtn {
  display: flex;
  justify-content: center;
  border-radius: 20px;
  background-color: pink;
  width: 20%;
  margin: 0 auto;
}

.AboutMe_Link {
  font-size: 3rem;
  color: #fff;
}

.AboutMe_Link::before {
  content: "ABOUT ME";
}

.AboutMe_Link:hover {
  color: #273c75;
}

.AboutMe_Link:hover::before {
  content: "더 알아보기";
}

.Skills {
  height: 100vh;
  background: #463f3a;
  /* fallback for old browsers */
  font-family: "IBM Plex Sans KR", sans-serif;
  padding: 2rem 2rem;
}

.Skills_Title {
  display: flex;
  justify-content: center;
}

.Skills_Title_Text {
  position: relative;
  top: 30px;
  border-bottom: 1px solid;
  font-size: 3rem;
  color: #fff;
}

.Skills_List {
  display: -ms-grid;
  display: grid;
  place-items: center;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: minmax(25%, auto);
  padding: 4rem 2rem;
}

@media screen and (max-width: 1200px) {
  .Skills_List {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
  }
}

.Skills_Item {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 7.5% 20px;
  overflow: auto;
  border-radius: 20px;
  color: #fff;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: 0.3s;
  -webkit-transform: rotate(0.001deg);
          transform: rotate(0.001deg);
}

.Skills_Item:hover {
  background-color: #fff;
  color: black;
  border-radius: 20px;
  padding: 2rem 2rem;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transform: rotate(0.02deg);
          transform: rotate(0.02deg);
}

.Skills_Name {
  font-size: clamp(1rem, 2.5vw, 1.5rem);
}

